<template>
    <div></div>
</template>

<script>
    export default {
        name: '',
        data() {
            return {
                
            }
        },
        async mounted() {
            if (this.$store.state.token) {
                const chronu = await this.chronusNow()
                this.$store.commit('saveChronus', [ chronu, true ])
            }
        },
        methods: {
            
            async chronusNow() {

                let res = {
                    '_where[start_lte]': this.view.getToday(),
                    '_where[end_gte]': this.view.getToday()
                }

                res = await this.conn.get(
                    this.api.chronus,
                    this.$store.state.token,
                    res
                )

                if (res) {
                    if (res.length > 0) {
                        return res[0] 
                    }
                } else {
                    this.view.alert_message('新的結算日程開始了，但是數據庫未有關於新日程的時間準備，請移步後臺新增時間代號。')
                }
                return null
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


