import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        token: '',
        user_backend: null,
        user: { },
        bonus: { },
        user_unit: null,

        chronus: null,
        transfer: null,

        users_sponser: null,
        users_enroller: null,

        me: { },

        layout: null,
        platform_data: null
    },
    mutations: {
        changeAuth(state, token) {
            state.token = token
            localStorage.setItem('oils_token', token)
        },
        clearToken(state) {
            state.token = ''
            state.user = { }
            localStorage.setItem('oils_token', '')
        },

        saveUnitUser(state, items) {
            state.user_unit = items
        },
        saveBackendUser(state, items) {
            localStorage.setItem('oils_user_backend', JSON.stringify(items))
            state.user_backend = items
        },
        
        // -------------------------------------------
        //储存 活跃用户
        saveBonus(state, the_bonus) {
            state.bonus = the_bonus
        },
        saveUser(state, the_user) {
            state.user = the_user
        },
        saveMe(state, mine) {
            state.me = mine
        },
        // 储存 用户
        saveUserSponser(state, the_sponser) {
            state.users_sponser = the_sponser
        },
        saveUserEnroller(state, the_enroller) {
            state.users_enroller = the_enroller
        },
        // 储存 翻译
        saveTransfer(state, the_transfer) {
            state.transfer = the_transfer
        },

        // -------------------------------------------
        // 储存 时间
        saveChronus(state, code) {
            const the_chronus = code[0]
            
            if (code[1]) {
                delete the_chronus.buys;
                delete the_chronus.bonus;
                delete the_chronus.created_at;
                delete the_chronus.updated_at;
                delete the_chronus.published_at;
    
                localStorage.setItem('oils_chronu', JSON.stringify(the_chronus))
                
            }
            state.chronus = the_chronus
        },
        // 储存平台类型
        saveLayout(state, v) {
            state.layout = v
        },
        // 储存数据
        saveFromPlatform(state, v) {
            state.platform_data = v
            localStorage.setItem('oils_conn_data', JSON.stringify(v))
        }
    },
    getters: { 
        checkToken(state) {
            let res = false

                const token = localStorage.getItem('oils_token')
                
                if ((token == undefined) || (token == '') || (token == null) || (token == 'undefined')) {
                    res = true
                } else {
                    state.token = token
                    
                    console.log('token = ', token.substring(0, 15) + '...')
                }
    
            try{
            } catch(e) {

            }
            return v => {
              return res
            }
        },
        checkUserBackend(state) {
            let res = false

            try{
                const user_backend = JSON.parse(localStorage.getItem('oils_user_backend'))
                
                if ((user_backend == undefined) || (user_backend == '') || (user_backend == null) || (user_backend == 'undefined')) {
                    res = true
                } else {
                    state.user_backend = user_backend
                }
            } catch(e) {
                
            }

            return v => {
              return res
            }
        },
        checkChronus(state) {
            let res = false

            try{
                const chron = JSON.parse(localStorage.getItem('oils_chronu'))
                if ((chron == undefined) || (chron == '') || (chron == null)) {
                    res = true
                } else {
                    state.chronus = chron
                }
            } catch(e) {
                
            }

            return v => {
                return res
            }
        },
        checkPlantfrom(state) {
            let res = false

            try{
                const item = JSON.parse(localStorage.getItem('oils_conn_data'))
                if ((item == undefined) || (item == '') || (item == null)) {
                    res = true
                } else {
                    state.platform_data = item
                }
            } catch(e) {
                
            }

            return v => {
                return res
            }
        },
    },
    modules: {

    }
})


