<template>
    <router-view></router-view>
</template>

<script>
    export default {
        name: '',
        data() {
            return {
                
            }
        },
        provide() {
            return {
                reload: this.reload
            }
        },
        mounted() {
            
        },

        methods: {
            reload() {
                this.alive = false
                this.$nextTick(function() { this.alive = true })
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


