<template>
    <div></div>
</template>

<script>
    export default {
        name: '',
        data() {
            return {
                
            }
        },
        mounted() {
            setTimeout(e => {
                this.$emit('recive_Father', {
                    params: {
                        unid: 1
                    }
                })
            }, 1000)
        },
        methods: {
            // 获取 WORDPRESS 数据
            returnMessage(event){
                var data = event.data;
                switch (data.cmd) {
                    case 'getFormJson':
                        console.log(data)
                        console.log(data.params)
                        this.$emit('recive_Father', data)
                        break;
                }
            },
        },
        created() {
            // 添加消息监听
            window.addEventListener("message", this.returnMessage);
        },
        unmounted: function() {
            // 取消消息监听
            window.removeEventListener('message', this.returnMessage);
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


