<template>
    <div v-if="!finished" class="init-shimmer">
        <qiong-loading></qiong-loading>
    </div>
    <div v-else></div>
</template>

<script>
// import rolling from '@/common/es/api_bonus/doing/_rolling_tree'
// import computed from '@/common/es/api_bonus/doing/_computed'
// import vip from '@/common/es/api_bonus/doing/_vip'

import recommend from '../../common/es/api_bonus/_bonus/_recomend'
import team_layer from '../../common/es/api_bonus/_bonus/_team_layer'
import active_house from '../../common/es/api_bonus/_bonus/_active_house'
import QiongLoading from '../Qiong/Ui/QiongLoading.vue'

    export default {
  components: { QiongLoading },
        props: {
            chronu_id: {
                type: String
            }
        },
        name: '',
        data() {
            return {
                chronu: '',
                transfer: [ ],

                finished: false
            }
        },
        created() {
            this.valuation(this.chronu_id)
        },
        mounted() {
            this.init()
        },
        methods: {
            valuation(chronu) {
                if (chronu) {
                    this.chronu = chronu
                } else {
                    this.chronu = this.$store.state.chronus.id
                }
            },
            async init() {
                
                this.finished = false
                let res_sponser, res_enroller
                const _user_bc = this.$store.state.user_backend

                if (_user_bc) {
                    
                    let users_s = await this.userLoading('SPONSER')
                    let users_e = await this.userLoading('ENROLLER')
                    let buys = await this.buyLoading()

                    // 整合 购买
                    users_s = this.build_buying(users_s, buys)
                    users_e = this.build_buying(users_e, buys)

                    // 获取 中间数组， 获取 VIP 需要的参数
                    res_sponser = await this.bonus.rolling(users_s, 'SPONSER', this.rebuild_user, _user_bc.sponser_id)
                    res_enroller = await this.bonus.rolling(users_e, 'ENROLLER', this.rebuild_user, _user_bc.enroller_id)

                    // 生成 Transfer
                    this.transfer = await this.bonus.computed(res_sponser, 'SPONSER', this.transfer, null)
                    this.transfer = await this.bonus.computed(res_enroller, 'ENROLLER', this.transfer, null)

                    // 拿到 VIP
                    this.transfer.map(e => { e.vip = this.bonus.vip(e); return e })

                    // 计算 
                    await recommend(res_enroller, this.transfer)
                    await team_layer(res_sponser, this.transfer)
                    await active_house(res_sponser, this.transfer)

                    // 融入 TRANSFER
                    this.into_transfer(res_sponser, 'SPONSER')
                    this.into_transfer(res_enroller, 'ENROLLER')

                    // 保存数据
                    this.saving(res_sponser, res_enroller)
                    
                    this.finished = true
                }
            },

            // 保存数据
            saving(_s, _e) {
                this.$store.commit('saveUserSponser', _s)
                this.$store.commit('saveUserEnroller', _e)
                this.$store.commit('saveTransfer', this.transfer)

                console.log('sponser =', _e, 'enroller =', _e)
            },

            // 读取数据
            async buyLoading() {
                const res = await this.conn.get(
                    this.api.buys,
                    this.$store.state.token,
                    {
                        'chronu': this.chrou
                    }
                )

                return res.map(e => {
                    return {
                        num: e.num,
                        id: e.id,
                        pv: e.product.pv,
                        first_buy: e.first_buy,
                        users_permissions_user: e.users_permissions_user.id,
                        buy_time: e.buy_time,
                        product: e.product.id,
                    }
                })
            },
            async userLoading(mode = 'SPONSER') {
                const condition = { }
                if (mode == 'SPONSER') {
                    condition['_where[relation_index_sponser_contains]'] = this.$store.state.user_backend.relation_index_sponser
                } else {
                    condition['_where[relation_index_enroller_contains]'] = this.$store.state.user_backend.relation_index_enroller
                }

                return await this.conn.get(
                    this.api.users,
                    this.$store.state.token,
                    condition
                )
            },

            // 清洗数据
            rebuild_user(user) {
                delete user.created_at
                delete user.updated_at
                delete user.role
                delete user.provider
                delete user.confirmed
                delete user.blocked
                delete user.email
            },
            build_buying(users, buys) {
                return users.map(user => {
                    let res = []
                    buys.map(buy => { if (user.id == buy.users_permissions_user) { res.push(buy) } })
                    user.buys = res
                    return user
                })
            },
            into_transfer(res, model) {
                if (model = 'SPONSER') {
                    
                    this.bonus._build_res(res, this.transfer, function (u, t) {

                        for(let i= 0; i< t.length; i++ ) {

                            const trans = t[i]
                            if (u.unid == trans.unid) {

                                u.transfer = trans
                                trans.bonus = u.bonus
                            }
                        }
                    })
                } else {

                    this.bonus._build_res(res, this.transfer, function (u, t) {

                        for(let i= 0; i< t.length; i++ ) {

                            const trans = t[i]
                            if (u.unid == trans.unid) {
                                
                                u.transfer = trans
                                trans.bonus.usd_recommend = u.bonus.usd_recommend
                            }
                        }
                    })
                }
            }
        }
    }
</script>

<style lang="sass" scoped>
.init-shimmer
    position: fixed
    top: 0
    left: 0
    width: 100vw
    height: 100vh
    z-index: 9999
    background-color: white
</style>


