<template>
    <div>
        <data-from-plant-form  @recive_Father="reciveData"></data-from-plant-form>
    </div>
</template>

<script>
import LoginFromStrapi from '../Conning/Login/LoginFromStrapi.vue'
import DataFromPlantForm from '../Conning/PlantForm/DataFromPlantForm.vue'
    export default {
        components: {
            DataFromPlantForm,
            LoginFromStrapi
        },
        name: '',
        data() {
          
            return {
                
            }
        },
        mounted() {
            
        },
        methods: {
            reciveData(data) {
                console.log('data =', data)
                try {
                    if (data.params) {

                        this.savingData(data.params)
                        this.$emit('sign_Father', true)
                    }
                } catch(e) { }
            },

            savingData(data) {
                let res = true
                /*
                const plant = this.$store.getters.checkPlantfrom()
                if (!plant) {
                    if (this.$store.state.platform_data.userID == data.userID) {
                        res = false
                    }
                }
                */

                if (res) {
                    data.username = this.conf.BOWSER.username
                    data.password = this.conf.BOWSER.password

                    this.$store.commit('saveFromPlatform', data)
                }
            }
        },
    }
</script>

<style lang="sass" scoped>
.data
    font-size: 36px
    color: rgb(7, 178, 67)
</style>


