<template>
    <div>

        <wordpress-app v-if="layout == conf.LAYOUT.WORDPRESS" @sign_Father="plantFinished"></wordpress-app>
        <div v-else></div>

        <div v-if="$store.state.chronus">
            <init-auto v-if="$store.state.user_backend"></init-auto>
        </div>

        <div>
            <login-from-strapi 
                ref="loginREF"
                @defeated_Father="loginDefeated" @success_Father="loginSuccess"></login-from-strapi>
        </div>

        <transition name="fade_out">
            <init-shimmer v-show="shimmer"></init-shimmer>
        </transition>

        <transition name="page_in">
            <app-view v-if="login_success != null"></app-view>
        </transition>

    </div>
</template>

<script>
import AppView from './AppView.vue'
import LoginFromStrapi from './components/Conning/Login/LoginFromStrapi.vue'
import InitAuto from './components/Data/InitAuto.vue'
import WordpressApp from './components/PlantForm/WordpressApp.vue'
import InitShimmer from './components/Shimmer/InitShimmer.vue'
    export default {
        components: {
            WordpressApp,
            InitShimmer,
                InitAuto,
                LoginFromStrapi,
                AppView
        },
        name: '',
        data() {
            return {
                layout: '',

                finished: false,
                shimmer: true,

                login_success: null
            }
        },
        async created() {
            // 更改 平台
            this.layout = this.conf.LAYOUT.WORDPRESS

            const _layout = this.$route.query.layout
            if (_layout) {
                this.layout = _layout
            }

            // 储存 Layout
            await this.$store.commit('saveLayout', this.layout)

            // HTML 模式去除 Shimmer
            if (this.layout == this.conf.LAYOUT.HTML) {
                this.finished = true
            }
        },
        methods: {
            
            plantFinished(v) {
                this.finished = v
                this.$refs.loginREF.touchLogin()

                setTimeout(e => {
                    this.shimmer = false
                }, 1000)
            },

            loginSuccess(v) {
                console.log('登录成功!!!')
                this.login_success = true 
            },

            loginDefeated(typed) {
                // TRUE = 有平台数据，登录失败
                // FALSE = 无平台数据，登录失败
                if (!typed) {
                    console.log('未找到 平台 userID，登录失败！！！')
                } else {
                    console.log('有 平台 数据，但是没有 Strapi User。登录失败')
                }

                this.login_success = false 
            }
        },
    }
</script>

<style lang="sass" scoped>
    
.fade_out-leave,
.fade_out-leave-to
    transition: 1s all
    
.fade_out-leave
    opacity: 1
.fade_out-leave-to
    opacity: 0
    
    
.page_in-enter,
.page_in-enter-to
    transition: 1s all
    
.page_in-enter
    opacity: 0.3
    transform: translateY(20px)
.page_in-enter-to
    opacity: 1
    transform: translateY(0px)

</style>


