
const Account = () => import('@/pages/Account/Account')

const AccountStatement = () => import('@/pages/Account/Statement/AccountStatement')

export default {
    
    path: '/home/account',
    component: Account,
    
    redirect:'/home/account/statement',
    children: [
        
        {
            path: '/home/account/statement',
            component: AccountStatement
        },
    ]
}


