<template>
    <v-app id="app">
        <chronus-now ref="chronuREF"></chronus-now>
        
        <layout></layout>
    </v-app>
</template>

<script>
    import ChronusNow from './components/Data/Using/ChronusNow.vue'
    import Layout from './Layout.vue'

    export default {
        components: { ChronusNow, Layout },
        name: '',
        data() {
            return {
                hasing_chronu: false,
            }
        },
        async mounted() {
            await this.app()
        },

        async created() {
            
            await this.$store.commit('saveChronus', [ '', false ])
        },
        methods: {
            
            async app() {

                // 加载月份
                const chronu = await this.$refs.chronuREF.chronusNow()
                if (chronu) {
                    this.$store.commit('saveChronus', [ chronu, true ])
                    this.hasing_chronu = true
                }
                console.log('加载月份 =', chronu)
            },
        }
    }
</script>

<style lang="sass">
@import "@/common/sass/base.sass"
</style>


