<template>
    <div v-if="end_login">
        <login-defeated v-if="sign_defeated != null" :defeated="sign_defeated"></login-defeated>
    </div>
    <div v-else></div>
</template>

<script>
import LoginDefeated from './LoginDefeated.vue'
    export default {
  components: { LoginDefeated },
        name: '',
        props: {

        },
        data() {
            return {
                user: null,

                unid: null,
                username: null,
                password: null,

                sign_defeated: null,

                end_login: false
            }
        },
        mounted() {

        },

        methods: {
            // 处理登录之前
            touchLogin() {
                this.username = this.$store.state.platform_data.username
                this.password = this.$store.state.platform_data.password

                console.log('预备登录')

                if (this.username) {
                    this.login()
                } else {
                    this.sign_defeated = false
                    this.$emit('defeated_Father', this.sign_defeated)
                }
            },
            // 处理 平台登录干预
            intervenLogin() {
                if (this.$store.state.layout == this.conf.LAYOUT.WORDPRESS) {
                    this.unid = this.$store.state.platform_data.unid
                } else {
                    this.unid = this.user.unid 
                }
            },

            // 处理登录之后
            switchLoginSuccess(user) {
                console.log('A')
                this.$store.commit('saveBackendUser', user)
                
                this.$emit('success_Father', true)
                console.log('B')

                if (user.sponser_id) {
                    onsole.log('C')
                    this.$router.push({ path: '/home/user/center' })
                }
            },

            // 登录
            async login() {

                try {
                    const res = await this.conn.login({
                        identifier: this.username,
                        password: this.password,
                    })

                    if (res) {
                        this.$store.commit('changeAuth', res.data.jwt)
                        this.user = res.data.user

                        // 干预 登录
                        this.intervenLogin()

                        // 获取真正的用户
                        this.unidForUser()
                    }
                } catch(e) {
                    // this.view.alert_message('賬號或密碼錯誤！！！')
                    this.sign_defeated = true
                    this.$emit('defeated_Father', this.sign_defeated)
                }

                this.end_login = true
            },
            
            async unidForUser() {

                let user = await this.conn.get(
                    this.api.users,
                    this.$store.state.token,
                    {
                        'unid': this.unid
                    }
                )
                if (user) {
                    if (user.length > 0) {

                        this.user = user[0]
                        console.log('有此用户 username =', this.user.username)

                        this.switchLoginSuccess(user[0])
                    }
                } else {
                    this.sign_defeated = true
                    this.$emit('defeated_Father', this.sign_defeated)
                }

            },
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


